import React from 'react'
import CustomBreadcrumb from '../../../components/CustomBreadcrumb/index'
import cookie from "react-cookies";
import {Tooltip, Radio, DatePicker, Icon, Input, Form, Button, message, InputNumber} from "antd";
import {isAuthenticated} from "../../../utils/Session";
import UserInfoCard from "../../../components/UserInfoCard";
import axios from "axios";
import ApiUtil from "../../../utils/ApiUtil";
import moment from 'moment'
import AvatarUpload from "../../../components/AvatarUpload";
import MyCard from "../../../components/MyCard/MyCard";
import Clock from "react-clock";
import 'react-clock/dist/Clock.css';
import './style.css'

const formItemLayout = {
    labelCol: {
        xs: {span: 20},
        sm: {span: 4},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 16},
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};
const key = 'updatable';

@Form.create()
class PInformationDemo extends React.Component {
    state = {
        user: {},
        time: new Date(),
        timer: null,
    }

    componentDidMount() {
        this.setState({
            timer: setInterval(() => {
                this.setState({
                    time: new Date(),
                });
//

            }, 1000)
        })
    }

    componentWillMount() {
        axios({
            method: "get",
            url: ApiUtil.URL_IP + '/api/getUser/' + isAuthenticated()
        }).then(res => {
            let user = res.data.data
            this.setState({user: user})
            this.props.form.setFieldsValue({
                nickname: user.nickname,
                sex: user.sex,
                age: user.age,
                birth: moment(user.birth, 'YYYY-MM-DD'),
                email: user.email,
                phone: user.phone,
                sign: user.sign
            });
        })

        this.setState({
            admined: cookie.load('userId-admined'),
            avatar: require('../../../assets/img/04.jpg')
        })

    }

    componentWillUnmount() {
        clearTimeout(this.state.timer)
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            let register = values
            register.birth = register.birth.format('YYYY-MM-DD')
            if (!err) {
                message.loading({content: '...', key});
                axios(
                    {
                        method: "post",
                        url: ApiUtil.URL_IP + '/api/updateUser/' + isAuthenticated(),
                        data: register
                    }
                ).then(
                    (res) => {
                        message.success({content: res.data.msg, key, duration: 2});
                    }
                ).catch((res) => {
                    message.error({content: res.data.msg, key, duration: 2});
                })
            }
        });
    };

    render() {

        const {getFieldDecorator} = this.props.form;
        return (
            <div>
                <CustomBreadcrumb arr={['', '']}/>
                <div className='outer'>
                    <div className={'sider'}>
                        <UserInfoCard name={isAuthenticated()}/>
                        <MyCard style={{
                            marginTop: -30, width: 291, height: 210, alignContent: 'center',
                            alignItems: 'center',
                            boxSizing: 'border-box',
                            display: 'flex',
                            flexDirection: 'row',
                            flexWrap: 'nowrap',
                            justifyContent: 'center',
                        }}>
                            <Clock renderNumbers={true} value={this.state.time}/>
                        </MyCard>
                    </div>
                    <div className={'content'}>
                        <MyCard style={{marginTop: 20}}>
                            <div style={{marginLeft: "30px"}}>
                                <div style={{fontWeight: "bold", marginBottom: "20px", fontSize: "18px"}}></div>
                                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                                    <Form.Item label=''>
                                        <AvatarUpload name={this.state.user.username}/>
                                    </Form.Item>
                                    <Form.Item label="">
                                         <span>{isAuthenticated()}&nbsp;
                                             <Tooltip title="">
                                                <Icon type="question-circle-o"/>
                                            </Tooltip>
                                            </span>

                                    </Form.Item>

                                    <Form.Item
                                        label={
                                            <span>&nbsp;
                                                <Tooltip title="">
                                                <Icon type="question-circle-o"/>
                                            </Tooltip>
                                            </span>
                                        }
                                    >
                                        {getFieldDecorator('nickname', {
                                            // initialValue: this.state.user.nickname,
                                            rules: [{
                                                required: true,
                                                message: '',
                                                whitespace: true
                                            }],
                                        })(<Input/>)}
                                    </Form.Item>
                                    <Form.Item label="">
                                        {getFieldDecorator('sex', {
                                            // initialValue: this.state.user.sex,
                                            rules: [{required: true}]
                                        })(
                                            <Radio.Group>
                                                <Radio value=""></Radio>
                                                <Radio value=""></Radio>
                                            </Radio.Group>,
                                        )}
                                    </Form.Item>
                                    <Form.Item label=''>
                                        {getFieldDecorator('age', {
                                            // initialValue: this.state.user.age,
                                            rules: [{required: true, message: ""}]
                                        })(<InputNumber min={1} max={120}/>)}
                                        <span className="ant-form-text"> </span>
                                    </Form.Item>
                                    <Form.Item label="">
                                        {getFieldDecorator('birth', {
                                            // initialValue: moment(this.state.user.birth, 'YYYY-MM-DD'),
                                            rules: [{type: 'object', required: true, message: 'Please select time!'}],
                                        })(<DatePicker placeholder={''}/>)}
                                    </Form.Item>
                                    <Form.Item label="">
                                        {getFieldDecorator('email', {
                                            // initialValue: this.state.user.email,
                                            rules: [
                                                {
                                                    type: 'email',
                                                    message: '!',
                                                },
                                                {
                                                    required: true,
                                                    message: '!',
                                                },
                                            ],
                                        })(<Input/>)}
                                    </Form.Item>
                                    <Form.Item label="">
                                        {getFieldDecorator('phone', {
                                            // initialValue: this.state.user.phone,
                                            rules: [{required: true, message: '!'}],
                                        })(<Input addonBefore={"+86"}
                                                  style={{width: '100%'}}/>)}
                                    </Form.Item>
                                    <Form.Item
                                        label={
                                            <span>&nbsp;
                                                <Tooltip title="">
                                                <Icon type="question-circle-o"/>
                                            </Tooltip>
                                            </span>
                                        }
                                    >
                                        {getFieldDecorator('sign', {
                                            // initialValue: this.state.user.sign,
                                            rules: [{
                                                required: false,
                                                message: '',
                                                whitespace: true
                                            }],
                                        })(<Input.TextArea/>)}
                                    </Form.Item>
                                    <Form.Item {...tailFormItemLayout}>
                                        <Button type="primary" htmlType="submit">
                                            
                                        </Button>
                                    </Form.Item>
                                </Form>
                            </div>

                        </MyCard>
                    </div>
                </div>


            </div>
        )
    }
}

export default PInformationDemo



